import { Component, OnInit,EventEmitter,Output } from '@angular/core';

@Component({
  selector: 'app-child',
  templateUrl: './child.component.html',
  styleUrls: ['./child.component.css']
})
export class ChildComponent implements OnInit {
  @Output() myClick = new EventEmitter<boolean>(); //cái này là truyền sự kiện từ thằng con lên thằng cha
  //Cái này cần khai báo thư viện EventEmitter và Output trên @angular/core
  public value: number = 0;
  constructor() { }

  ngOnInit() {
  }
  addForParents(){
    this.myClick.emit(true);// phát đi sự kiện myClick
  }
  subForParents(){
    this.myClick.emit(false);
  }
}
