import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-struct',
  templateUrl: './struct.component.html',
  styleUrls: ['./struct.component.css']
})
export class StructComponent implements OnInit {
  // public isShow : boolean = true;
  public arrSubjects: any[] = ["Angular","NodeJS","React"];
  public checkboxValue: boolean = false;
  public english: string = "";
  public vietnamese: string = "";
  public isShowForm: boolean = false;
  public filterStatus: string = "XemTatCa";
  public arrWords: any[] = [
    {
      id:1,
      en: "action",
      vn: "hành động",
      memorized:true
    },
    {
      id:2,
      en: "actor",
      vn: "diễn viên",
      memorized:false
    },
    {
      id:3,
      en: "activity",
      vn: "hoạt động",
      memorized:true
    },
    {
      id:4,
      en: "active",
      vn: "chủ động",
      memorized:true
    },
    {
      id:5,
      en: "bath",
      vn: "tắm",
      memorized:false
    },
    {
      id:6,
      en: "bedroom",
      vn: "phòng ngủ",
      memorized:true
    }
  ]
  constructor() { }

  ngOnInit() {
  }
  addWord(){
    var newWord: any = {
      id:this.arrWords.length + 1,
      en:this.english,
      vn:this.vietnamese,
      memorized:this.checkboxValue
    }
    this.arrWords.unshift(newWord);//thêm vào đầu mảng
    this.english = "";
    this.vietnamese = "";
    this.checkboxValue = false;
    this.isShowForm = false;
  }
  newFunction(){
    this.checkboxValue = !this.checkboxValue;
  }
  delete(id){
    // this.arrWords.filter(word=>{
    //   if(word.id === id){;
    //     const index: number = this.arrWords.indexOf(word);
    //     if (index !== -1) {
    //         this.arrWords.splice(index, 1);
    //     }
    //   }
    // });
    const index: number = this.arrWords.findIndex( word=>word.id === id);//tìm kiếm bằng index
    if (index !== -1) {
      this.arrWords.splice(index, 1);
    }  
  }
  getShowStatus(memo: boolean): boolean{
    const dkXemTatCa = this.filterStatus === "XemTatCa";
    const dkXemTuDaNho = this.filterStatus === "XemTuDaNho" && memo;
    const dkXemTuChuaNho = this.filterStatus === "XemTuChuaNho" && !memo;
    return dkXemTatCa || dkXemTuDaNho || dkXemTuChuaNho;
  }
  changeStatusMenorized(id: number){
    this.arrWords.filter(word=>{
      if(word.id === id){
        word.memorized = !word.memorized;
        return;
      }
    })
  }
}
