import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
@Injectable({
  providedIn: 'root'
})
export class WeatherService {

  constructor(private http:Http) { }
  getTemp(cityName:string){
    var arr: any[] = [];
    const url = "http://api.openweathermap.org/data/2.5/weather?appid=86ef7f80102472990ed17a52ec832d87&units=metric&q="+cityName;
    return this.http.get(url)
    .toPromise()
    .then(res=>res.json())
    .then(resJson=>resJson.main)
    .catch(err=>console.log(err));
  }
}
